export default function fixFabricSize (fabric, width, height) {
  width = `${width}px`
  height = `${height}px`
  const { wrapperEl: { style: wrapperStyle }, lowerCanvasEl: { style: lowerStyle }, upperCanvasEl: { style: upperStyle } } = fabric
  wrapperStyle.width = width
  wrapperStyle.height = height
  lowerStyle.width = width
  lowerStyle.height = height
  upperStyle.width = width
  upperStyle.height = height

}
